// Import materials & setup
import MaterialUI from "./components/MaterialUI";
import Reactbootstrap from "./components/ReactBootstrap";
import "./styles.css";
import Button from "@mui/material/Button";
import FormatQuoteIcon from "@mui/icons-material/FormatQuote";
import TwitterIcon from "@mui/icons-material/Twitter";
import Quotes from "./components/quotes-api";
import { useState, setState } from "react";

// Main App
// Set state & define functions
export default function App() {
  const [state, setState] = useState({
    quote: "place",
    author: "holder"
  });

  if (
    state ===
    {
      quote: "",
      author: ""
    }
  ) {
    return NewQuote;
  }

  function NewQuote() {
    const randomIndex = Math.floor(Math.random() * Quotes.length);
    const randomQuote = Quotes[randomIndex];

    setState({
      quote: randomQuote.quote,
      author: randomQuote.author
    });
  }

  function TweetQuote() {
    // TODO
  }

  // Display on screen
  return (
    <div id="outside">
      <div id="background" class="container">
        <div id="quote-box" className="col-md-8 offset-md-2">
          <p id="text"> {state.quote} </p>
          <p id="author"> {state.author} </p>
          <Button
            variant="contained"
            endIcon={<FormatQuoteIcon />}
            id="new-quote"
            className="buttons"
            onClick={NewQuote}
          >
            New Quote
          </Button>

          <Button
            variant="contained"
            endIcon={<TwitterIcon />}
            id="tweet-quote"
            className="buttons"
            onClick={TweetQuote}
          >
            <a
              id="tweet-quote"
              target="_blank"
              href="twitter.com/intent/tweet?text={state.quote}{state.author}"
            >
              Tweet Quote
            </a>
          </Button>
        </div>
      </div>
    </div>
  );
}

